import {
    Component, ViewEncapsulation,
    AfterViewInit, Inject
} from '@angular/core';

import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';


@Component({
    selector: 'app-dialog',
    templateUrl: './remove-channel.dialog.html'
})
export class RemoveChannelDialog {

    constructor(
        public dialogRef: MatDialogRef<RemoveChannelDialog>,
        @Inject(MAT_DIALOG_DATA) public data: any) { }

    onNoClick(): void {
        this.dialogRef.close();
    }

}


